body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


li{
  list-style: none; 
  padding: 10px;
}

li a{
  text-decoration: none;
  color: black;
}

.main-menu ul{
  display: flex;
  background-image: linear-gradient(to right, rgb(179, 88, 248) , rgb(0, 209, 178));
  margin: 0;
}

footer{
  border: 1px solid blueviolet;
  /* position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center; */
}
.mainBody{
  height: 100%;
}
footer h4{
  margin: 0;
}

.upload-Form-container{
  display: flex;
  justify-content: center;
  padding-top: 5em;

}
.upload-Form-container form input, 
.deleteForm input{
  display: block;
  padding: 6px;
  width: 275px;
  margin: 5px auto;
}

.upload-Form-container form button{
  margin-left: 5px;
  width: -webkit-fill-available;
  height: 30px;
  border-radius: 2em;
  border: none;
  background-color: rgb(171, 95, 253);
  text-transform: uppercase;
}


.deleteForm{
  text-align: center;
  padding-top: 5em;
}

.deleteForm button{
  width: 100px;
  padding: 10px;
  margin: 10px;
  border-radius: 2em;
  border: none;
  background-color: rgb(255, 60, 60);
  text-transform: uppercase;
}


